"use client";

import { m } from "framer-motion";
import { Linkedin, Twitter, Github } from "lucide-react";

interface TeamCardProps {
  name: string;
  role: string;
  bio: string;
  gradient: string;
  index: number;
  socials?: {
    linkedin?: string;
    twitter?: string;
    github?: string;
  };
}

export function TeamCard({ name, role, bio, gradient, index, socials }: TeamCardProps) {
  return (
    <m.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5, delay: index * 0.1 }}
      className="group text-center"
    >
      <div className={`w-32 h-32 rounded-3xl bg-gradient-to-br ${gradient} mx-auto mb-6 flex items-center justify-center shadow-xl group-hover:scale-105 transition-transform`}>
        <span className="text-5xl font-bold text-white">
          {name.charAt(0)}
        </span>
      </div>
      <h3 className="text-xl font-semibold text-foreground mb-1">
        {name}
      </h3>
      <p className="text-primary font-medium mb-3">
        {role}
      </p>
      <p className="text-sm text-muted-foreground mb-4">
        {bio}
      </p>
      <div className="flex justify-center gap-3">
        <a 
          href={socials?.linkedin || "#"} 
          className="w-9 h-9 rounded-full bg-muted flex items-center justify-center text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors"
        >
          <Linkedin className="w-4 h-4" />
        </a>
        <a 
          href={socials?.twitter || "#"} 
          className="w-9 h-9 rounded-full bg-muted flex items-center justify-center text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors"
        >
          <Twitter className="w-4 h-4" />
        </a>
        <a 
          href={socials?.github || "#"} 
          className="w-9 h-9 rounded-full bg-muted flex items-center justify-center text-muted-foreground hover:text-primary hover:bg-primary/10 transition-colors"
        >
          <Github className="w-4 h-4" />
        </a>
      </div>
    </m.div>
  );
}
