"use client";

import { m } from "framer-motion";

interface TimelineItemProps {
  year: string;
  title: string;
  description: string;
  index: number;
}

export function TimelineItem({ year, title, description, index }: TimelineItemProps) {
  const isEven = index % 2 === 0;
  
  return (
    <m.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5, delay: index * 0.1 }}
      className={`flex flex-col lg:flex-row gap-8 items-center ${
        isEven ? "lg:flex-row" : "lg:flex-row-reverse"
      }`}
    >
      <div className={`flex-1 ${isEven ? "lg:text-right" : "lg:text-left"}`}>
        <div className="bg-card border border-border rounded-2xl p-6 hover:shadow-xl hover:border-primary/20 transition-all inline-block">
          <div className="text-sm text-primary font-semibold mb-2">{year}</div>
          <h3 className="text-xl font-bold text-foreground mb-2">{title}</h3>
          <p className="text-muted-foreground">{description}</p>
        </div>
      </div>
      <div className="w-4 h-4 rounded-full bg-primary ring-4 ring-primary/20 flex-shrink-0 z-10" />
      <div className="flex-1" />
    </m.div>
  );
}
